.titlebody {
    display: flex;
    flex-direction: column;
}

.title {
    text-align: left;
    padding: 1vh;
}

.projectarray {
    display: flex;
    flex-direction: row;
    padding: 1vw;
}

.projectbutton {
    width: 30vw;
    height: 50vh;
    background-color: lightsalmon;
    padding: 1vw;
    text-color: white;
    font-size: xx-large;
    margin: 1vw;
}

.fullstory {
    font-family: Baskerville;
    margin: 2vw;
}

.storytitle {
    font-weight: bold;
    font-size: large;
}

.storybody {
    font-size: medium;
}

.image {
    width: 30%;
}

#cs {
    order: 1;
}

#math {
    order: 2;
}
